import React, { useState, useEffect } from "react";
// reactstrap components
import {
  Button,
  ButtonGroup,
  Card,
  CardHeader,
  CardBody,
  CardTitle,
  FormGroup,
  Form,
  Input,
  Row,
  Col,
  Label,
  Table,
  Modal,
  Badge,
} from "reactstrap";
// react component used to create sweet alerts
import classNames from "classnames";
import io from "socket.io-client";
import { connect } from "react-redux";
import ReactDatetime from "react-datetime";
import Select from "react-select";
import ReactTable from "components/ReactTable/ReactTable.js";
import "./sniper.css";
import NotificationAlert from "react-notification-alert";
import moment from 'moment';
const ETHSCAN = (props) => {
  //necessary functions import
  const { apiConfig, ApiCall } = global;
  const [socket, setSocket] = useState("");
  const notificationAlertRef = React.useRef(null);
  const showNotify = async (title, text, type = "success", place = 'tr', autoDismiss = 5) => {
    const options = {
      place: place,
      message: (
        <div><div>{title}</div><div>{text}</div></div>
      ),
      type: type,
      icon: "tim-icons icon-bell-55",
      autoDismiss: autoDismiss,
    };
    notificationAlertRef.current.notificationAlert(options);
  }
  const explorerURL = 'https://ethscan.com';
  //pair table
  const [pairData, setPairData] = useState([]);
  //delete modal
  const [delModalStatus, setDelModalStatus] = useState(false);
  const showDelModal = (data) => { setDelModalStatus(true); };
  const closeDelModal = () => { setDelModalStatus(false); }
  //pair
  const readPair = async () => {
    try {
      const response = await ApiCall(
        apiConfig.ethscan_readPair.url,
        apiConfig.ethscan_readPair.method,
        props.credential.loginToken,
        {}
      );
      if (response.data.data) setPairData(response.data.data);
    } catch (error) {
      if (error.response) showNotify(error.response.data.message, '', 'danger');
      else if (error.request) showNotify("Request failed", '', 'danger');
      else showNotify("Something went wrong", '', 'danger');
    }
  };
  //init
  useEffect(() => {
    setSocket(io(apiConfig.endPoint));
    readPair();
  }, []);
  //function with socket
  useEffect(() => {
    if (socket) {
      socket.on("connect", () => {
        socket.on("ethscan:pairStatus", (data) => {
          setPairData(data.data);
        });
      });
    }
    return () => {
      if (socket) socket.disconnect();
    };
  }, [socket]);
  return (
    <>
      <div className="rna-container">
        <NotificationAlert ref={notificationAlertRef} />
      </div>
      <div className="content">
        <Row>
          <Col md={12}>
            <Card>
              <CardBody className="max-height-600">
                <Table responsive>
                  <thead className="text-primary">
                    <tr>
                      <th>Name/Symbol</th>
                      <th>Address</th>
                      <th>Verified</th>
                      <th>Link</th>
                      <th>Age</th>
                    </tr>
                  </thead>
                  <tbody>
                    {
                      pairData.map((item, key) => {
                          return (
                            <tr key={key}>
                              <td>
                                <div className="symbolText">
                                  {item.name1}<span >{item.symbol1}</span>
                                </div>
                              </td>
                              <td>
                                {item.token1}
                              </td>
                              <td>
                                <Badge color={item.status == 1 ? "success" : "danger"}>
                                  {
                                    item.verified == 1 ? "Yes" : "No"
                                  }
                                </Badge>
                              </td>
                              <td>
                                <a className="linkA" href={`${explorerURL}/address/${item.token1}`} target="_blank">BscScan</a>
                                 &nbsp;
                                {
                                  item.enableTrading &&
                                  <a className="linkA1" href={`https://Uniswap.org`} target="_blank">Pancake</a>
                                }
                              </td>
                              <td>{item.life}</td>
                            </tr>
                          );
                      })
                    }
                  </tbody>
                </Table>
              </CardBody>
            </Card>
          </Col>
        </Row>
      </div>
    </>
  );
};
const mapStateToProps = (state) => {
  const { LoginReducer } = state;
  return { credential: LoginReducer };
};
export default connect(mapStateToProps)(ETHSCAN);
